import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
	state: {
		activeIndex: '', //导航高亮
		articleUuid: '',
		userAccountUuid: '',
		ticketName: '', //订单详情里的罚单 跳 罚单页
		orderNumber: '', //平台账户列表 跳 订单列表
		tableMaxHeight: 0 //表格最大高度
	},
	mutations: {
		setActiveIndex (state, data) {
			state.activeIndex = data
		},
		setArticleUuid (state, data) {
			state.articleUuid = data
		},
		setUserAccountUuid (state, data) {
			state.userAccountUuid = data
		},
		setTicketName(state, data) {
			state.ticketName = data
		},
		setOrderNumber(state, data) {
			state.orderNumber = data
		},
		setTableMaxHeight(state, data) {
			state.tableMaxHeight = (window.innerHeight || document.documentElement.clientHeight || document.body.clientHeight || 0) - data - 60
		}
	}
})



