export default {
    //获取格式化时间 yyyy-MM-dd
    getNowTime(time) {
        if (!time) {
            return '--'
        }
        var date = new Date(time)
        var time = {
            year: date.getFullYear(),
            month: date.getMonth() + 1,
            day: date.getDate()
        }
        if (time.month < 10) {
            time.month = '0' + time.month
        }
        if (time.day < 10) {
            time.day = '0' + time.day
        }
        return time.year + '-' + time.month + '-' + time.day
    },

    //删除表格元素
    deleteItem(url, that, json) {
        // console.log(url, that)
        that.$confirm('是否确认删除?', '提示', {
                type: 'warning'
            })
            .then(() => {
                that.$http.post(url, json)
                    .then(data => {
                        var data = data.body.body
                        if (data.result == 0) {
                            that.$message({
                                type: 'success',
                                message: '删除成功!'
                            })
                        } else {
                            that.$message(data.description)
                        }
                        that.getList && that.getList()
                    }, data => {
                        console.error('请求失败 Σ(っ°Д°;)っ')
                        that.getList && that.getList()
                    })
            }).catch(() => {})
    },

    //对象浅拷贝
    clone(obj1, obj2) {
        return Object.assign(obj1,obj2)
    },


    /*
     * AJAX - post method
     * 
     * url:         API
     * json:        JSON data
     * that:        this
     * callback1:   success callback
     * callback2:   error callback
     * callback3:   APIerror callback
     */
    _post(url, json, that, ...callback) {
        that.$http.post(that.API + url, json)
            .then(data => {
                var data = data.body.body
                if (data.result == 0) {
                    console.log(
                        `%c\n ╭----------post------------- 数据 -------------------------\n`, 
                        `background: #C9F5C9`,
                        data, 
                        `\n ╰--------------------------- 结束 -------------------------`,
                    )
                    callback[0] && callback[0](data)
                } else {
                    that.$message({
                        message: data.description,
                        type: 'error'
                    })
                    callback[1] && callback[1](data)
                }
            }, data => {
                that.$message('查询异常，请重试  Σ(っ°Д°;)っ')
                callback[2] && callback[2](data)
                console.error('%c[[[server-error]]]', 'background: red; color: #fff; padding: 3px 0', data)
            })
    },
    // 格式化富文本
    articleFormat(text) {
        return text.replace(/[ ]/g,'&nbsp;')
    },
    // 格式化金额
    formatterMoney(value) {
        return Math.floor(value*100)/100
    },
    copyText(value) {
        if(value) {
            var input = document.createElement('input')
            input.setAttribute('value', value)
            document.body.appendChild(input)
            input.select()
            document.execCommand("copy")
            document.body.removeChild(input)
            return true
        } else {
            return false
        }
    },
    checkPower(path) {
        if (path === '/' || path === '/login' || path === '/news') { //过滤掉登录页
            return true
        }
        const power = JSON.parse(sessionStorage.getItem('power'))
        const p = this.parsePath(path)
        let powerArr = []
        for(var k in power) {
            power[k].map( v => {
                powerArr.push(v)
            })
        }
        if (!powerArr.includes(p)) {
            return false
        }
        return true
    },
    // 解析对应的path
    parsePath(path) {
        switch(path) {
            case '/index': return '首页轮播'
            case '/system-push': return '系统推送'
            case '/operating-articles': return '运营文章'
            case '/vehicle-manage': return '车型库管理'

            case '/user-info': return '用户信息'
            case '/car-owner-info': return '车东信息'
            case '/vehicle-info': return '车辆信息'
            case '/ticket-info': return '罚单信息'
            case '/user-credit': return '用户信用'

            case '/order': return '订单'

            case '/platform-account': return '平台账户'
            case '/withdraw': return '提现管理'

            case '/account-manage': return '账户管理'
            case '/my-account': return '我的账户'
            default: return path
        }
    }
}


