import Vue from 'vue'
import Router from 'vue-router'
import Vuex from 'vuex'


//路由:
//登录
const login = resolve => require(['@/views/login.vue'], resolve)

//运营信息
const index = resolve => require(['@/views/index.vue'], resolve)
const bannerList = resolve => require(['@/views/operate-info/banner-list.vue'], resolve)
const systemPush = resolve => require(['@/views/operate-info/system-push.vue'], resolve)
const operatingArticles = resolve => require(['@/views/operate-info/operating-articles.vue'], resolve)
const addArticle = resolve => require(['@/views/operate-info/add-article.vue'], resolve)
const vehicleManage = resolve => require(['@/views/operate-info/vehicle-manage.vue'], resolve)

//用户
const userInfo = resolve => require(['@/views/user/user-info.vue'], resolve)
const vehicleInfo = resolve => require(['@/views/user/vehicle-info.vue'], resolve)
const ticketInfo = resolve => require(['@/views/user/ticket-info.vue'], resolve)
const userCredit = resolve => require(['@/views/user/user-credit.vue'], resolve)
const carOwnerInfo = resolve => require(['@/views/user/car-owner-info.vue'], resolve)

//订单
const order = resolve => require(['@/views/order/order.vue'], resolve)

//财务
// const userAccount = resolve => require(['@/views/finance/user-account.vue'], resolve)
// const userAccountInfo = resolve => require(['@/views/finance/user-account-info.vue'], resolve)
// const userAccountIncomeExpense = resolve => require(['@/views/finance/user-account-income-expense.vue'], resolve)
const platformAccount = resolve => require(['@/views/finance/platform-account.vue'], resolve)
const withdraw = resolve => require(['@/views/finance/withdraw.vue'], resolve)

//系统设置
const accountManage = r => require.ensure([], () => r(require('@/views/settings/account-manage.vue')), 'system-setting')
const myAccount = r => require.ensure([], () => r(require('@/views/settings/my-account.vue')), 'system-setting')

//消息
const news = resolve => require(['@/views/news/news.vue'], resolve)

Vue.use(Router)
Vue.use(Vuex)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'login',
      alias: '/login',
      component: login
    }, {
      path: '/index',
      component: index,
      children: [
        {
          path: '/',
          name: 'bennerList',
          component: bannerList,
        }, {
          path: '/system-push',
          name: 'systemPush',
          component: systemPush,
        }, {
          path: '/operating-articles',
          name: 'operatingArticles',
          component: operatingArticles
        }, {
          path: '/add-article',
          name: 'addArticle',
          component: addArticle
        }, {
          path: '/vehicle-manage',
          name: 'vehicleManage',
          component: vehicleManage
        }, {
          path: '/user-info',
          name: 'userInfo',
          component: userInfo
        }, {
          path: '/vehicle-info',
          name: 'vehicleInfo',
          component: vehicleInfo
        }, {
          path: '/ticket-info',
          name: 'ticketInfo',
          component: ticketInfo
        }, {
          path: '/user-credit',
          name: 'userCredit',
          component: userCredit
        }, {
          path: '/car-owner-info',
          name: 'carOwnerInfo',
          component: carOwnerInfo
        }, {
          path: '/order',
          name: 'order',
          component: order
        // }, {
          // path: '/user-account',
          // name: 'userAccount',
          // component: userAccount
        // }, {
          // path: '/user-account-info',
          // name: 'userAccountInfo',
          // component: userAccountInfo
        // }, {
          // path: '/user-account-income-expense',
          // name: 'userAccountIncomeExpense',
          // component: userAccountIncomeExpense
        }, {
          path: '/platform-account',
          name: 'platformAccount',
          component: platformAccount
        }, {
          path: '/withdraw',
          name: 'withdraw',
          component: withdraw
        }, {
          path: '/account-manage',
          name: 'accountManage',
          component: accountManage
        }, {
          path: '/my-account',
          name: 'myAccount',
          component: myAccount
        }, {
          path: '/news',
          name: 'news',
          component: news
        }
      ]
    }
  ]
})


